.plan_wrapper {
    text-align: left;
    margin: 20px;
    font-family: Snell Roundhand, cursive;
    color: teal;
}

.time_wrapper {
    display: flex;
    justify-content: center;
}

.plan_wrapper h3 {
    margin-bottom: 10px;
}

.plan_wrapper span {
    display: block;
    margin-top: 20px;
    font-weight: bold;
}

.plan_wrapper ul {
    list-style-type: none;
    padding-left: 0;
}

.plan_wrapper li {
    margin-bottom: 5px;
}
